<template>
  <div class="page">
    <div>
      <el-carousel :interval="5000" arrow="hover">
        <el-carousel-item v-for="item in 4" :key="item">
          <h3>{{ item }}</h3>
        </el-carousel-item>
      </el-carousel>
    </div>
    <div>
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>推荐作品</span>
          <el-button style="float: right; padding: 3px 0" type="text">更多</el-button>
        </div>
        <div v-for="o in 4" :key="o" class="text item">
          <div class="img">
            <img src="http://piggylearnnet.com/upload/image/20190423/1556000499210079747.png" />
          </div>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
page {
  width: 1000px;
  height: 500px;
  background-color: slategrey;
}
.el-carousel {
  width: 59%;
  margin-top: 70px;
  margin-left: 10%;
  border-radius:4px;
  display:block;
}
.el-carousel__item h3 {
  color: #475669;
  font-size: 18px;
  opacity: 0.75;
  line-height: 300px;
}
.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}
.el-carousel__item:nth-child(2n+1) {
  background-color: #d3dce6;
}
.box-card {
  margin-top: 20px;
  margin-left: 10%;
  width: 59%;
  height: 100%;
}
.box-card .img {
  width: 200px;
  height: 80px;
  overflow:hidden
}
</style>



